.caret-up {
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-bottom-color: #fff;
  visibility: hidden;
  top: 2rem;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  transition: all 1s ease-in-out;
  opacity: 0;
}

.dropdown-content button {
  background-color: transparent;
  border-style: none;
  width: 100%;
  cursor: pointer;
  outline: none;
}

/* Dropdown Button */
.dropbtn {
  color: #fff;
  background-color: transparent;
  font-size: 0.6rem;
  border: none;
  padding: .8rem;
  outline: transparent;
  font-weight: bolder;
}

.dropbtn > * {
  margin: .2rem;
}
 
/* The container <div> - needed to position the dropdown content */
.dropdown {
  display: none;
  position: relative;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  visibility: hidden;
  height: 0;
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-top: #fff solid 0.1rem;
  opacity: 0;
  top: 3.2rem;
  left: 50%;
  transform: translate(-50%, 0);
  right: auto;
  width: 13.5rem;
  text-align: center;
  transition: all 1s ease-in-out;
}

.dropdown-content > * {
  text-decoration: none;
}

/* Links inside the dropdown */
.dropdown-content button {
  font-size: 0.8rem;
  padding: 1rem;
  text-decoration: none;
  display: block;
  color: #fff;
  transition: all 0.4s ease;
}

/* Change color of dropdown links on hover */
.dropdown-content button:hover {
  background: rgba(0, 0, 0, 0.8);
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content,
.dropdown:hover .caret-up {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.dropbtn:hover {
  cursor: pointer;
}

@media (max-width: 840px) {
  .dropdown {
    display: block;
  }
}

